<template>
  <div>
    <!-- dom   updated   -->
    <div v-show="false">{{option}}</div>
    <div :id="'chart'+ id"  class="echartsHight chartHeight"></div>
  </div>
</template>

<script>
let id = 0;

let echarts = require('echarts/lib/echarts')
// 
require('echarts/lib/chart/bar')

export default {
  name: "ty-chart",
  props: ["option", "height"],
  data() {
    return {
      id,
      chart: {},
      echarts
    };
  },
  computed: {},
  created() {
    id++;
  },
  methods: {
    init() {
      let that = this;
      let dom = document.getElementById(`chart${this.id}`);
      //  
      this.chart = echarts.init(dom)
      this.showLoading();
      window.addEventListener("resize", this.resize);
    },
    resize() {
      this.chart.resize();
    },
    setOption() {
      
      this.chart.setOption(this.option, true);
      this.chart.hideLoading();
      this.resize();
    },
    showLoading() {
      if (!this.chart) {
        return;
      }
      this.chart.showLoading();
    },
    //
    refresh() {
      this.showLoading();
      this.setOption();
    },
    destroy() {
      
      this.chart.dispose();
      
      window.removeEventListener("resize", this.resize);
    }
  },
  // 
  updated() {
    var self = this;
    if (self.refresh) {
      self.refresh();
    }
  },
  mounted() {
    // 
    this.init();
    if (this.option) {
      this.setOption();
    }
  },
  destroyed() {
    this.destroy();
  },
  // 
  deactivated() {
    
    if (!this.$store.state.cachePage.indexOf(this.$route.name)) {
      this.destroy();
    }
  }
};
</script>

<style scoped>
 .chartHeight{
   height: 160px;
 }
</style>



